@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  font-family: tahoma,"Poppins", sans-serif;
}

.app {
  background-color: #eff2f2;
}

.app__body {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.app__createPost {
  display: flex;
  justify-content: center;
}

.post {
  max-width: 600px;
  background-color: white;
  margin-bottom: 16px;
}

.post__image {
  width: 100%;
  object-fit: contain;
  border-top: 0.5px solid #eff2f2;
  border-bottom: 0.5px solid #eff2f2;
}

.post__header {
  display: flex;
  padding: 12px;
  justify-content: space-between;
}

.post__headerInfo {
  margin-left: 10px;
}

.post__bottom {
  padding: 6px 12px;
}

.commentInput {
  display: flex;
  justify-content: space-between;
  padding: 16px 12px;
}

.commentInput__text {
  flex: 1 1;
}
.commentInput__button {
  margin-left: 24px;
}

.commentInput__textarea {
  border: none;
  width: 100%;
  font-size: 14px;
  resize: none;
}

.commentInput__textarea {
  border: none;
}

.commentInput__textarea:focus {
  outline: none;
}

.comment {
  padding: 3px 12px;
}

.feed__posts {
  margin-top: 24px;
  align-self: center;
}

.feed {
  display: flex;
  justify-content: center;
}

.header {
  background-color: white;
  padding: 16px 20px;
  border-bottom: 1px solid #dde0e1;
  object-fit: contain;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
  position: -webkit-sticky;
  position: sticky;
  background-color: white;
  z-index: 100;
  top: 0;
}

.header > img {
  height: 35px;
}

.header__Right {
  display: flex;
}

.header__RightProfileImg {
  height: 30px;
  width: 30px;
  border-radius: 30px;
}

.button {
  background-color: transparent;
  cursor: pointer;
  border: none;
}

.button:focus {
  outline: none;
}

.imageUpload {
  background-color: white;
  max-width: 600px;
  width: 100%;
  margin-top: 30px;
  padding: 16px 0px;
}

.createAPost__Top {
  margin: 0px 16px;
}

.image-upload > input {
  display: none;
}

.createAPost__center {
  margin: 0px 16px;
}

.imageUpload__bottom {
  display: flex;
  margin: 0px 16px;
  justify-content: space-between;
  align-items: center;
}

.createAPost__textarea {
  border: none;
  width: 100%;
  font-size: 16px;
  margin-top: 8px;
}

.createAPost__textarea {
  border: none;
}

.createAPost__textarea:focus {
  outline: none;
}

.button {
  background-color: transparent;
  cursor: pointer;
  border: none;
}

.button:focus {
  outline: none;
}

.imagePreview {
  margin-top: 8px;
  margin-bottom: 12px;
  display: flex;
  align-items: center;
}

.circularProgress {
  margin-left: 8px;
}

#image-1-preview {
  display: none;
  max-height: 60px;
  position: relative;
  border-radius: 5px;
}

